:root {
  --primary: #000000;
  --secondary: #232F3E;
  --tertiary: #B12704;
  --light: #fafafa;
  --dark: #272f32;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Karla', sans-serif;
  font-weight: 400;
  line-height: 1.5;
  font-size: 16px;
  color: ivory;
  text-align: center;
  background-color: rgb(61, 61, 61);
}

body img {
  border: 5px solid slategray;
  border-radius: 5px;
  filter: grayscale(60%);

}

body img:hover {
  filter: grayscale(100%);
  box-shadow: 2px 2px 5px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Rubik', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
  color: #FFFDD0
}

h1 {
  font-size: 2.25rem;
  font-family: 'Monsieur La Doulaise', cursive;
}

h2 {
  font-size: 3rem;
  text-align: center;
  font-family: 'Poiret One', cursive;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}


p {
  font-size: 1.1rem;
  margin-top: 0;
  font-family: 'Poiret One', cursive;
  font-size: 150%;
  color: #FFFDD0;
}

/* Edited font and font size for card headers */
.card p {
  font-size: 1.1rem;
  margin-top: 0;
  font-family: 'Poiret One', cursive;
  font-size: 125%;
  color: #FFFDD0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

.card a {
  color: #FFFDD0;
  font-family: 'Poiret One', cursive;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--primary);
}
header a{
  color: var(--light);
}
header h1 {
  padding-left: 34%;
}

#brand-menu {
  padding-top: 50px;
  padding-bottom: 50px
}

button {
  font-size: 1rem;
  text-decoration: none;
  color: #FFFDD0;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  outline: none;
  cursor: pointer;
  border: 2px solid #FFFDD0;
  margin: 5px;
  background-color: rgb(61, 61, 61);
}

.card button {

}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--dark);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.px-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.error-text {
  color: var(--error);
}

@media screen and (max-width: 768px) {
  .card {
    width: 100%;
  }
}

